.category-page {
    &__results {
        .sort-panel {
            display: flex;
            gap: 10px;
            margin-bottom: 20px;

            & > span {
                line-height: 100%;
                color: $gray; }

            .dropdown-toggle {
                gap: 10px;
                line-height: 100%;

                &::after {
                    margin: 0;
                    border: none;
                    content: url("data:image/svg+xml,%3Csvg width='11' height='7' viewBox='0 0 11 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.519531 1L5.51953 6L10.5195 1' stroke='black'/%3E%3C/svg%3E%0A"); } }

            .dropdown-menu {
                top: 26px;
                width: 200px;
                padding: 10px 0 10px 10px;

                li {
                    a {
                        padding: 0;
                        font-size: 16px;
                        line-height: 157%; } } } }

        .result-list {
            display: flex;
            flex-wrap: wrap;
            gap: 30px; }

        .show-more {
            width: 330px;
            height: unset;
            padding-block: 19px 17px;
            margin: 30px auto 0 auto;
            font-size: 17px; } } }

@include media-breakpoint-up(xl) {
    .category-page {
        .filter-toggle {
            display: none; }

        &__results {
            .product-card {
                width: 255px; } } } }

@include media-breakpoint-down(xl);
.category-page {
    .filter-toggle {
        gap: 8px;
        width: 100%;
        height: 50px;
        background: $white;
        border: 1px solid $black;
        border-radius: 8px;
        margin-bottom: 20px;
        font-size: 18px;

        .icon {
            width: 18px;
            height: 15px; } } }

@include media-breakpoint-only(lg) {
    .category-page {
        &__results {
            .product-card {
                width: 290px; } } } }

@include media-breakpoint-down(lg) {
    .category-page {
        &__results {
            .show-more {
                width: 100%; } } } }

@include media-breakpoint-only(md) {
    .category-page {
        &__results {
            .product-card {
                width: 330px; } } } }

@include media-breakpoint-only(md) {
    .category-page {
        &__results {
            .product-card {
                width: 330px; } } } }

@include media-breakpoint-only(sm) {
    .category-page {
        &__results {
            .product-card {
                width: 240px; } } } }

@include media-breakpoint-down(sm) {
    .category-page {
        &__results {
            .result-list {
                row-gap: 30px;
                column-gap: 20px; }

            .product-card {
                width: 170px; }

            .show-more {
                height: 50px;
                padding: 0;
                font-size: 16px; } } } }

