.splash-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    &__img {
        width: 832px; }

    &__title {
        width: fit-content;
        margin: 60px 0 40px 0;
        font-weight: 700;
        font-size: 50px;
        text-align: center;
        line-height: 110%;
        color: $black; }

    &__button {
        width: 290px;
        height: 65px;
        background: $primary;
        border-radius: 5px;
        font-weight: 700;
        font-size: 20px;
        line-height: 120%;
        color: $white; } }


@include media-breakpoint-down(lg) {
    .splash-screen {
        &__img {
            width: 100%; } } }

@include media-breakpoint-down(md) {
    .splash-screen {
        &__title {
            max-width: 320px;
            font-size: 40px; } } }
