.categories {

    .section__content {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
        grid-gap: 1px;
        padding: 1px; }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        max-width: 186px;
        outline: 1px solid #E9E9E9;

        &:hover {
            z-index: 2;
            outline: 1px solid #EB8815; }

        &_text {
            max-width: 140px;
            font-size: 16px;
            line-height: 100%;
            text-align: center;
            color: #000; } } }

@include media-breakpoint-up(lg) {
    .categories {
        &__item {
            height: 153px;
            padding-block: 20px; } } }

@include media-breakpoint-only(md) {
    .categories {
        &__item {
            height: 142px;
            padding-block: 18px; } } }

@include media-breakpoint-only(sm) {
    .categories {
        &__item {
            height: 142px;
            padding-block: 18px; } } }

@include media-breakpoint-down(sm) {
    .categories {

        .section__content {
            width: 330px;
            margin-inline: auto; }

        &__item {
            height: 135.72px;
            padding-block: 15px; } } }
