.shop-locations {
    .section__content {
        gap: 30px; }

    .left {
        flex-direction: column;
        gap: 30px; }

    .section__heading {
        margin: 0; }

    &__contacts {
        flex-direction: column; }

    .contacts {
        &__link {
            font-size: 26px;
            font-weight: bold;
            line-height: 100%; }

        &__text {
            font-size: 16px;
            color: $black;
            line-height: 120%; } }

    &__map-container {
        display: flex;
        gap: 30px; }

    &__addresses {
        display: flex;
        flex-direction: column; }

    &__address {
        width: 255px;
        padding: 20px 0 18px 20px;
        border: none;
        border-radius: 10px;

        &.active {
            background:  $gray-200; }

        h3 {
            font-weight: bold;
            font-size: 18px;
            line-height: 100%;
            color: $black; }

        span {
            font-size: 16px;
            line-height: 100%;
            color: $gray; } }

    &__map {
        height: 300px;
        border: none;
        border-radius: 10px;
        background: $gray-300; } }

@include media-breakpoint-up(xl) {
    .shop-locations {
        .left {
            width: 350px; }

        &__map {
            width: 445px; } } }

@include media-breakpoint-only(lg) {
    .shop-locations {
        .left {
            width: 210px; }

        &__map {
            width: 405px; } } }

@include media-breakpoint-down(lg) {
    .shop-locations {
        .section__content {
            flex-direction: column; }

        .left {
            width: 100%; } } }

@include media-breakpoint-only(md) {
    .shop-locations {
        &__contacts {
            flex-direction: row;
            gap: 45px; }

        &__map {
            width: 405px; } } }

@include media-breakpoint-down(md) {
    .shop-locations {
        &__map-container {
            flex-direction: column;
            gap: 20px;
            width: 100%; }

        &__addresses-container {
            position: relative;
            width: 100%;
            height: 79px;
            overflow: visible; }

        &__addresses {
            position: absolute;
            flex-direction: row; }

        &__map {
            width: 100%; } } }

@include media-breakpoint-only(sm) {
    .shop-locations {
        &__contacts {
            flex-direction: row;
            gap: 45px; }

        &__map {
            height: 340px; } } }

@include media-breakpoint-down(sm) {
    .shop-locations {
        .left {
            height: 229px; }

        &__map {
            height: 240px; } } }
