.shopping-cart {
    &__checkout {
        width: 100%;
        padding: 30px;
        background: $black;
        border-radius: 10px;
        color: $white;

        &[fixed] {
            position: fixed;
            top: 0; }

        &[absolute] {
            position: absolute;
            bottom: 0;
            right: 0; }

        &-row {
            display: flex;
            justify-content: space-between;
            margin-bottom: 10px;
            font-size: 18px;
            line-height: 22px;

            & .right {
                display: flex;
                flex-direction: column;
                flex-shrink: 0;
                width: 165px; } }

        &-address {
            font-size: 12px;
            line-height: 14px;
            color: #696969; }

        &-text {
            width: 100%;
            margin-top: 50px;
            font-size: 16px;
            line-height: 18px; }

        &-btn {
            width: 100%;
            height: 56px;
            margin-top: 20px;
            border-radius: 5px;
            font-size: 17px;
            text-transform: uppercase;
            background: $primary; } }

    &__price {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid $gray;

        &-row {
            display: flex;
            justify-content: space-between;
            width: 100%;

            &--big {
                margin-bottom: 20px;
                font-size: 24px;
                line-height: 130%; }

            &--small {
                margin-top: 10px;
                font-size: 18px;
                line-height: 22px; } } } }

@include media-breakpoint-up(xl) {
    .shopping-cart {
        &__checkout {
            width: 350px; } } }

@include media-breakpoint-only(lg) {
    .shopping-cart {
        &__checkout {
            width: 290px;

            &-row {
                & .right {
                    width: 130px; } }

            &-text {
                margin-top: 40px; } } } }

@include media-breakpoint-down(lg) {
    .shopping-cart {
        &__checkout {
            padding-left: 35px;

            &-btn {
                margin-top: 30px; } } } }

@include media-breakpoint-only(md) {
    .shopping-cart {
        &__checkout {
            width: 390px;

            &-row {
                & .right {
                    width: 212px; } }

            &-text {
                width: 325px; } } } }



@include media-breakpoint-down(md) {
    .shopping-cart {
        &__checkout {
            &-row {
                & .right {
                    width: 182px; } } } } }

@include media-breakpoint-only(sm) {
    .shopping-cart {
        &__checkout {
            width: 390px; } } }

