.daily-product {
    .section__content {
        justify-content: space-between;
        width: 100%; } }

.dp {
    &-product {
        position: relative;
        border-radius: 10px;

        &__whatsapp {
            width: 36px;
            height: 36px;
            background: #58BE55;
            border-radius: 5px;
            color: #fff;

            & .icon {
                width: 20px;
                height: 20px; } }

        &__left {
            position: relative;
            overflow: hidden;
            border-radius: 10px 0 0 10px; }

        &__right {
            border-radius: 0 10px 10px 0;
            background: linear-gradient(150.64deg, #231F21 0%, #543F2C 100%);

            .circle-discount {
                position: absolute;
                top: -46px;
                right: -46px;
                width: 93px;
                height: 93px;
                border-radius: 50%;
                background: linear-gradient(180deg, #FF8A01 0%, #FF5C01 100%);
                font-weight: 700;
                font-size: 28px;
                color: #fff;
                z-index: 3; } }

        //------------------------------------------------

        &__product-info {
            flex-direction: column;
            justify-content: space-between;
            align-items: stretch;
            width: 100%;
            padding: 35px 29px;
            color: #fff;

            &-bottom {
                flex-grow: 1; } }

        &__product-title {
            flex-grow: 1;
            line-height: 120%;
            font-size: 22px;
            font-weight: 600;
            overflow: hidden; }

        &__product-price {
            align-items: center;
            line-height: 22px;
            margin-top: 31px;

            &_price {
                padding: 7px 15px;
                background-image: url("../images/discount-bg.png");
                background-repeat: no-repeat;
                background-size: 100% 100%;
                -o-background-size: 100% 100%;
                -webkit-background-size: 100% 100%;
                font-size: 22px;
                font-weight: bold;
                letter-spacing: -0.65px; }


            &_old-price {
                margin-left: 12px;
                font-size: 22px;
                color: $white;
                text-decoration: line-through;
                letter-spacing: -0.9px; } }

        &__installment {
            margin-top: 7px;

            &_icon {
                height: 18px;
                width: 18px;
                margin-right: 10px;
                border-radius: 50%;
                background: #fff;
                color: #008555; }

            &_icon .icon {
                width: 11px;
                height: 11px; }

            &_text {
                margin-right: 10px;
                font-size: 16px;
                line-height: 120%; } }

        &__button-group {
            width: 292px;
            margin-top: 23px;

            &_row {
                justify-content: space-between;
                align-items: center; } }

        &__amount-group {
            margin-bottom: 19px;
            padding: 1px;
            border-radius: 5px;

            .btn {
                width: 36px;
                height: 36px;
                background: #6D4F3C;
                font-size: 22px;
                color: #fff;
                outline: 1px solid #4C3A2A;
                cursor: pointer; }

            .icon {
                width: 7px; }

            .icon-plus {
                width: 10px; }

            & #product_amount_decrease {}

            & #product_amount {
                font-weight: bold; }

            & #product_amount_increase {} }



        &__one-click-buy {
            width: 232px;
            height: 46px;
            border: 1px solid #fff;
            border-radius: 8px;
            font-size: 16px;
            background: transparent;
            color: #fff; }

        &__like {
            & .btn {
                padding: 0;
                margin: 0px 16px 6px 0px;
                color: #fff; }

            & .icon {
                width: 22px;
                height: 20px; } }

        &__add-to-cart {
            width: 100%;
            height: 50px;
            margin-top: 15px;
            background: #EB8815;
            border-radius: 8px;
            font-size: 15px;
            color: $white;
            font-weight: bold;
            letter-spacing: 0.4px; }

        //------------------------------------------------

        &__img {
            height: 100%;
            width: 100%; }

        & .product-tag {
            position: absolute;
            top: 24px;
            left: 14px;

            &__new {
                text-align: center; } } }

    &__discount {
        &-countdown {
            text-align: center; }

        &-text {
            border-right: 1px solid rgba(255, 255, 255, 0.5);
            text-align: center; } }

    &-description {
        background: #000;
        border-radius: 10px;

        &__text {
            font-size: 15px;
            line-height: 120%;
            color: #CFCFCF; } } }

.product-tag {
    &__discount {
        width: 204px; }

    &__new {
        width: 103px;
        height: 31px;
        line-height: 2rem; } }

.dp {
    &__discount {
        &-text,
        &-countdown {
            height: 31px;
            line-height: 2rem; } } }

.dp {
    &__discount {
        &-text {
            width: 102px; }

        &-countdown {
            width: 101px; } } }

@include media-breakpoint-up(xl) {
    .daily-product {
        padding-top: 44px; }

    .dp {
        &-product {
            height: 380px;

            &__left {
                width: 350px; }

            &__right {
                width: 379px; } }

        &-description {
            position: relative;
            height: 380px;
            width: 350px;

            &__img {
                position: absolute;
                top: -46px; }

            &__text {
                position: absolute;
                left: 25px;
                bottom: 35px;
                width: 300px;
                background: #000;
                -webkit-box-shadow: 0px -60px 78px -16px rgba(0,0,0,0.73);
                -moz-box-shadow: 0px -60px 78px -16px rgba(0,0,0,0.73);
                box-shadow: 0px -60px 78px -16px rgba(0,0,0,0.73); } } } }

@include media-breakpoint-down(xl) {
    .daily-product {
        .section__content {
            flex-direction: column;
            gap: 20px; } } }

@include media-breakpoint-down(lg) {
    .product-tag {
        flex-direction: column;
        gap: 5px; }

    .dp {
        &-product {
            .circle-discount {
                right: 0; } } } }


@include media-breakpoint-only(lg) {
    .daily-product {
        padding-top: 46px; }

    .dp {
        &-product {
            height: 368px;

            &__left {
                width: 530px;
                height: 368px; }

            &__right {
                width: 400px;
                height: 368px; }

            &__product-info {
                padding: 28px 30px; } }

        &-description {
            padding: 30px; } } }

@include media-breakpoint-only(md) {
    .daily-product {
        padding-top: 46px; }
    .dp {
        &-product {
            height: 394px;

            &__left {
                width: 330px;
                height: 394px; }

            &__right {
                width: 360px; }

            &__product-info {
                padding: 30px; }

            &__product-title {
                line-height: 110%; } }

        &-description {
            padding: 30px; } } }

@include media-breakpoint-down(md) {
    .daily-product {
        padding-top: 32px; }
    .dp {
        &-product {
            flex-direction: column;

            &__left {
                width: 100%;
                height: 260px;
                border-radius: 10px 10px 0 0; }

            &__right {
                width: 100%;
                border-radius: 0 0 10px 10px; }

            &__product-info {
                padding: 30px; }

            .circle-discount {
                top: -33px;
                width: 65px;
                height: 65px;
                font-size: 22px; } }

        &-description {
            padding: 30px; } } }

@include media-breakpoint-down(sm) {
    .dp {
        &-product {
            &__button-group {
                width: 100%; }

            &__like {
                margin-right: 8px; } } } }
