@import "./product-list/product-list";
@import "./payment-methods/payment-methods";
@import "./delivery-methods/delivery-methods";
@import "./contact-details/contact-details";
@import "./checkout-box/checkout-box";

.shopping-cart {
    h1 {
        letter-spacing: 0; }

    &__content {
        position: relative;
        display: flex;
        gap: 30px;

        & > .left {
            display: flex;
            flex-direction: column;
            gap: 30px;
            width: 100%; }

        & > .right {
            flex-shrink: 0; } } }


@include media-breakpoint-up(xl) {
    .shopping-cart {
        &__content {
            & > .right {
                width: 350px; } } } }

@include media-breakpoint-only(lg) {
    .shopping-cart {
        &__content {
            & > .right {
                width: 290px; } } } }

@include media-breakpoint-down(lg) {
    .shopping-cart {
        &__content {
            flex-direction: column;
            & > .right {
                width: 100%; } } } }
