.product-card {
    background: #fff;


    &__thumb {
        position: relative;
        width: 100%;
        border-radius: 10px 10px 0 0;
        overflow: hidden;

        &_img {
            width: 100%;
            height: 100%; } }

    .product-tag {
        position: absolute;
        top: 17px;
        left: 23px;
        width: 156px;
        font-size: 12px;

        &__discount {
            display: block;
            width: 81px;
            height: 22px;
            line-height: 24px;
            text-align: center; }

        &__new {
            width: 67px;
            height: 22px;
            line-height: 24px;
            text-align: center;
            letter-spacing: 0.04em; } }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: stretch;
        width: 100%;
        border: 1px solid #DEDEDE;
        border-top: none;
        border-radius: 0 0 10px 10px;

        &-bottom {
            flex-grow: 1; } }

    &__title {
        flex-grow: 1;
        height: 36px;
        line-height: 100%;
        font-size: 18px;
        font-weight: bold;
        overflow: hidden; }

    &__price {
        display: flex;
        align-items: center;
        gap: 10px;
        line-height: 20px;

        .price {
            font-size: 22px;
            font-weight: bold; }


        .old-price {
            font-size: 16px;
            color: $gray;
            text-decoration: line-through; } }

    &__button-group {
        margin-top: 20px; }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center; }

    &__one-click-buy {
        height: 38px;
        border: 1px solid #000;
        border-radius: 5px;
        font-size: 16px;
        background: #fff; }

    &__like {
        display: flex;
        align-items: center;
        gap: 5px;
        font-size: 13px;
        color: $gray;

        & .icon {
            width: 16px;
            height: 14px; } }

    &__add-to-cart {
        width: 100%;
        height: 42px;
        margin-top: 10px;
        background: #000;
        border-radius: 5px;
        font-size: 16px;
        color: $white;
        font-weight: bold; } }

.product-installment {
    display: flex;
    align-items: center;
    margin-top: 11px;
    color: #008555;

    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 18px;
        width: 18px;
        margin-right: 10px;
        background: #008555;
        color: #fff; }

    &__icon .icon {
        width: 11px;
        height: 11px; }

    &__text {
        width: 64px;
        margin-right: 10px;
        font-size: 12px;
        line-height: 12px; }

    &__plan {
        padding: 3px 10px;
        background: #F4F4F4;
        border-radius: 16px;
        font-size: 16px;
        line-height: 110%; } }

.products__section {
    position: relative;

    .splide {
        &__arrow {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            background: transparent;
            border-radius: 50%;
            outline: none !important;
            fill: $black;

            &:disabled {
                border-color: $gray !important;

                svg {
                    fill: $gray !important; } }

            &--prev {
                svg {
                    margin-right: 4px; } }
            &--next {
                svg {
                    margin-left: 4px;
                    transform: scale(-1, 1); } } } } }

.show-more {
    width: 360px;
    height: 50px;
    margin-top: 30px;
    background: $white;
    border: 1px solid $black;
    border-radius: 8px;
    font-size: 16px;
    line-height: 120%;
    color: $black;
    text-transform: uppercase; }

@include media-breakpoint-up(xl) {
    .product-card {
        &__thumb {
            height: 193px; }

        &__info {
            padding: 20px 15px 18px 15px; }

        &__price {
            margin-top: 30px; }

        &__one-click-buy {
            width: 175px; } }

    .products__section {
        .splide {
            &__arrow {
                top: 277px;
                height: 50px;
                width: 50px;
                border: 3px solid $black;

                &--prev {
                    left: -100px; }

                &--next {
                    right: -100px; }

                svg {
                    width: 16px;
                    height: 23px; } } } } }

@media screen AND (min-width: 1200px) AND (max-width: 1365px) {
    .products__section {
        .splide {
            &__arrow {
                top: 0px;

                &--prev {
                    right: 70px;
                    left: unset; }

                &--next {
                    right: 0px; } } } } }

@include media-breakpoint-down(xl) {
    .products__section {
        .splide {
            &__arrow {
                top: 0px;
                height: 40px;
                width: 40px;
                border: 2px solid $black;

                &--prev {
                    right: 60px; }

                &--next {
                    right: 0px; }

                svg {
                    width: 12px;
                    height: 18px; } } } } }

@include media-breakpoint-only(lg) {
    .product-card {
        &__thumb {
            height: 200px; }

        &__info {
            padding: 15px; }

        &__price {
            margin-top: 11px; }

        &__one-click-buy {
            width: 211px; } } }

@include media-breakpoint-only(md) {
    .product-card {
        &__thumb {
            height: 200px; }

        .product-tag {
            flex-direction: row !important;
            gap: 8px !important; }

        &__info {
            padding: 15px; }

        &__price {
            margin-top: 11px; }

        &__row {
            justify-content: flex-start;
            gap: 15px; }

        &__one-click-buy {
            width: 236px; } } }

@include media-breakpoint-down(md) {
    .products__section {
        .section__heading {
            max-width: 400px; } }

    .product-installment {
        flex-wrap: wrap;
        row-gap: 10px;

        &__text {
            width: 106px;
            margin: 0; } } }

@include media-breakpoint-only(sm) {
    .product-card {
        &__thumb {
            height: 190px; }

        .product-tag {
            flex-direction: row !important;
            gap: 8px !important; }

        &__info {
            padding: 15px; }

        &__price {
            margin-top: 11px; }

        &__one-click-buy {
            width: 165px; } } }

@include media-breakpoint-down(sm) {
    .product-card {
        &__thumb {
            height: 150px; }

        .product-tag {
            top: 10px;
            left: 10px; }

        &__info {
            padding: 15px 10px 10px 10px; }

        &__price {
            margin-top: 20px; }

        &__button-group {
            margin-top: 10.41px; }

        &__one-click-buy {
            width: 124px; } }

    .products__section {
        .section__heading {
            max-width: 265px; }

        .splide {
            &__arrow {
                &--prev {
                    right: 50px; } } } }

    .products__section:not(.is-active) {
        .splide {
            &__list {
                display: flex !important;
                flex-wrap: wrap;
                row-gap: 30px;
                column-gap: 20px; }
            &__slide {
                max-width: 170px; } } } }
