.category-page {
    &__content-top {
        display: flex;
        justify-content: space-between;
        gap: 30px;
        width: 100%;
        margin-bottom: 20px; }


    .video-banner {
        position: relative;
        width: 100%;
        height: 260px;
        color: $white;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 15px; }

        h2 {
            position: absolute;
            top: 20px;
            left: 20px;
            max-width: 230px;
            font-size: 24px;
            line-height: 110%; }

        .btn {
            position: absolute;
            gap: 10px;
            bottom: 20px;
            left: 20px;
            width: 185px;
            height: 40px;
            background: $red;
            font-size: 16px;
            line-height: 110%;
            color: $white;
            border-radius: 8px;

            .icon {
                width: 16px;
                height: 17px; } } }

    .promotion-carousel {
        position: relative;
        flex-shrink: 0;
        border: 1px solid $primary;
        border-radius: 15px;

        .splide {
            &__slide {
                position: relative;
                overflow: hidden;
                width: 100%;
                height: 260px;

                h2 {
                    position: absolute;
                    top: 20px;
                    left: 20px;
                    width: 175px;
                    font-size: 24px;
                    line-height: 110%;
                    color: $primary;
                    z-index: 1; }
                img {
                    position: absolute;
                    bottom: 55px;
                    right: 20px; } }

            &__arrow {
                position: absolute;
                height: 25px;
                width: 25px;
                bottom: 20px;
                background: transparent;
                border: 1px solid $black;
                border-radius: 50%;
                color: $black;
                outline: none !important;
                z-index: 2;

                & svg {
                    height: 10px;
                    width: 7.5px;
                    fill: currentColor; }

                &:disabled {
                    border-color: $gray;
                    color: $gray; }

                &--prev {
                    left: 20px; }

                &--next {
                    left: 50px;

                    svg {
                        transform: scale(-1, 1); } } }

            &__pagination {
                position: absolute;
                gap: 5px;
                bottom: 20px;
                right: 20px;

                &__page {
                    width: 5px;
                    height: 5px;
                    padding: 0;
                    border-radius: 50%;
                    background: $gray;

                    &.is-active {
                        background: $black; } } } } } }

@include media-breakpoint-up(xl) {
    .category-page {
        .promotion-carousel {
            width: 255px; } } }

@include media-breakpoint-down(xl) {
    .category-page {
        &__content-top {
            margin-bottom: 30px; } } }

@include media-breakpoint-only(lg) {
    .category-page {
        .promotion-carousel {
            width: 290px; } } }

@include media-breakpoint-only(md) {
    .category-page {
        .promotion-carousel {
            width: 270px; } } }

@include media-breakpoint-down(md) {
    .category-page {
        &__content-top {
            flex-direction: column;
            gap: 20px; }

        .video-banner {
            height: 200px;

            h2 {
                font-size: 22px; } }

        .promotion-carousel {
            width: 100%;
            height: 220px; } } }

