.category-page {
    .page__title h1 {
        font-size: 40px;
        font-weight: bold;
        line-height: 100%; }

    &__main {
        display: flex;
        gap: 30px; } }

@include media-breakpoint-down(xl) {
    .category-page {
        &__main {
            flex-direction: column; } } }
