.header {
    &__hidden-dropdown {
        width: 100%;
        display: none; }

    &__nav-bottom {
        background: $white;
        z-index: 999; }

    &__opt-link {
            padding: 17px 21px 12px 21px;
            background: #EB8815;
            border-radius: 5px;
            font-size: 14px;
            color: $white;

            &:hover {
                color: $gray; } }

    &__burger-menu-container {
        position: relative;
        width: fit-content;
        margin: 0;
        z-index: 999; }

    &__burger-menu {
        background: $white; }


    .menu {
        font-size: 18px;
        color: $black;

        a {
            font-size: 18px;
            color: $black; }

        &__categories {
            width: 321px;
            background: $white; }

        &__sub-container {
            &:hover > .menu__sub-categories {
                display: block !important;
                background: $gray-200; } }


        &__link {
            position: relative;
            display: inline-block;
            width: 100%;
            padding: 15px 30px 15px 29px;
            cursor: pointer;

            &-arrow::after {
                position: absolute;
                top: 50%;
                transform: translateY(-100%);
                right: 30px;
                content: url("data:image/svg+xml,%3Csvg width='8' height='14' viewBox='0 0 8 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.34326 1.30469L7.00012 6.96154L1.34326 12.6184' stroke='black' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                width: 5.66px;
                height: 11.31px;
                margin-bottom: 9px;
                z-index: 100; }

            &:hover {
                background: $gray-200; }

            & img {
                width: 30px;
                height: 30px;
                margin-right: 10px; }

            & span {
                width: 206px; } }

        &__link-inner {
                width: 100%;
                margin: 0 auto; }

        &__sub-categories {
            position: absolute;
            display: none;
            top: 0;
            left: 321px;
            background: $gray-200;
            z-index: 10; }

        &__sub-link {
            display: inline-block;
            width: 100%;
            height: 60px;
            line-height: 60px;
            padding: 0 28px;

            &:hover {
                background: $gray-400; } } } }




@include media-breakpoint-up(xl) {

    .header {

        &__nav {
            justify-content: space-between;
            margin-top: 20px;
            color: $white;

            a {
                color: $white;
                line-height: 100%; }

            .header__burger-menu-btn {
                align-items: center;
                gap: 10px;
                padding: 13px 12.5px 11px 12.5px;
                background: $primary;
                font-size: 18px;
                color: $white;
                border-radius: 5px;

                .icon {
                    width: 10px;
                    height: 9px; }

                span {
                    white-space: nowrap; } }

            &-bottom {
                display: none !important; } }

        &__burger-menu {
            position: absolute;
            left: 0;
            top: 50px; }

        .menu {

            &__categories {
                padding: 15px 0;
                box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
                border-radius: 5px 0px 0px 5px; }

            &__sub-container {
                position: relative; }

            &__link {
                position: relative; }

            &__sub-categories {
                width: 349px;
                padding: 15px 0; } }


        &__nav-bar {
            gap: 30px;
            padding: 0 21.5px;
            background: $black;
            border-radius: 5px; } }

    .nav-bar {
        &__link {
            display: inline-block;
            padding: 15px 0 11px 0;
            font-size: 18px;
            line-height: 100%;
            white-space: nowrap;
            cursor: pointer;

            &:hover {
                color: $gray; } } } }


@include media-breakpoint-down(xl) {

    .header {
        &__nav {
            position: absolute;
            flex-direction: column;
            top: 121px;
            left: 0;
            max-width: 100%;

            & > .header__opt-link {
                display: none !important; }

            .header__burger-menu-btn {
                display: none !important; } }

        &__nav-bar {
            display: none !important; }

        &__burger-menu {}
        top: -50px;

        &__nav-bottom {
            width: 100%;
            padding-block: 30px;
            border-top: 1px solid $gray;

            .left {
                flex-direction: column;
                gap: 20px; }

            .header__addresses {
                flex-direction: column;
                gap: 15px; }

            .center {
                flex-direction: column;
                gap: 20px; }

            .header {
                &__badges {
                    width: 298px; }

                &__community {
                    gap: 8px;

                    .community-link {
                        width: 30px;
                        height: 30px;
                        color: $white; }

                    .icon-tiktok {
                        color: $black; } } } } } }


@include media-breakpoint-only(lg) {

    .header {

        &__burger-menu-container {
            width: calc( 100vw - ( (100vw - 930px)/2 ) );
            margin-left: calc( (100vw - 930px)/2 ); }

        .menu {
            &__sub-categories {
                height: 100%;
                width: calc( 100% - 321px ); } }

        &__nav-bottom {
            .left {
                width: 321px; }

            .center {
                width: 399px; } } } }

@include media-breakpoint-up(md) {
    .header__nav-bottom .right {
        align-items: flex-start; } }

@include media-breakpoint-only(md) {

    .header {

        &__burger-menu-container {
            width: calc( 100vw - ( (100vw - 690px)/2 ) );
            margin-left: calc( (100vw - 690px)/2 ); }

        .menu {
            &__sub-categories {
                height: 100%;
                width: calc( 100% - 321px ); } }

        &__nav-bottom {
            .left {
                width: 240px; }

            .center {
                width: 359px; } } } }

@include media-breakpoint-down(md) {

    .header {

        &__nav {
            top: 137px; }

        &__burger-menu-container {
            width: 100%; }

        .menu {
            &__categories,
            &__sub-categories {
                position: unset;
                width: 100%; }

            &__link::after {
                display: none; }

            &__link-inner {
                position: relative;
                padding-inline: 30px; }

            &__link-arrow {
                .menu__link-inner {
                    &::after {
                        position: absolute;
                        top: 0;
                        right: 29px;
                        content: url("data:image/svg+xml,%3Csvg width='13' height='8' viewBox='0 0 13 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 1.17188L6.34315 6.82873L0.686292 1.17187' stroke='black' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                        width: 13px;
                        height: 8px;
                        margin-bottom: 9px; }

                    &:hover::after {
                        content: url("data:image/svg+xml,%3Csvg width='13' height='8' viewBox='0 0 13 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.685547 6.82812L6.3424 1.17127L11.9993 6.82812' stroke='black' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A"); } } } }

        &__nav-bottom {
            .container {
                flex-direction: column; }

            .center {
                margin-top: 25px; } } } }

@include media-breakpoint-only(sm) {

    .header {
        .menu {
            &__link-inner {
                width: 510px; } } } }

@include media-breakpoint-down(sm) {

    .header {
        .menu {
            &__link-inner {
                width: 360px; } } } }
