.drag-menu {
    margin-bottom: 20px;

    .container {
        overflow: visible; }

    &__item {
        width: fit-content;

        a,
        span {
            display: inline-block;
            padding: 8px 20px;
            background: $gray-200;
            border-radius: 30px;
            color: $black;
            letter-spacing: 0.04px; }

        span {
            background: $black;
            color: $white; } } }
