.other-reviews {

    .section__content {
        display: flex;
        flex-wrap: wrap;
        gap: 30px; }

    .btn {
        width: 255px;
        height: 70px;
        border-radius: 8px;
        font-size: 18px;
        color: $white; }

    .vk {
        background:  #0077FE; }

    .yandex {
        background: #323555; }

    .two-gis {
        background: #299400; }

    .google {
        background:  #E94336; } }

@include media-breakpoint-only(lg) {
    .other-reviews {

        .section__content {
            justify-content: space-between;
            gap: 0; }

        .btn {
            width: 224px; } } }

@include media-breakpoint-only(md) {
    .other-reviews {

        .section__content {
            column-gap: 30px;
            row-gap: 20px; }

        .btn {
            width: 330px; } } }

@include media-breakpoint-down(md) {
    .other-reviews {

        .section__content {
            gap: 10px; }

        .btn {
            width: 100%;
            height: 60px; } } }
