.product-reviews {
    padding-block: 80px;
    background: #F4F4F4; }


.reviews {
    position: relative;

    & > .left {
        flex-direction: column;
        gap: 40px; }

    & > .right {
        display: flex;
        flex-direction: column;
        gap: 40px; }

    &__tab-nav {
        display: flex;
        gap: 10px;

        .btn {
            padding: 8px 20px;
            border-radius: 30px;
            font-size: 16px;
            color: $black;
            border: 1px solid $black;
            background: transparent;

            &.active {
                background: $black;
                color: $white;
                cursor: default; } } }

    &__more {
        width: 100%;
        height: 69px;
        border: 1px solid $black;
        border-radius: 5px;
        background: transparent;
        font-size: 24px; }

    &__add-button {
        width: 152px;
        height: 40px;
        border-radius: 5px;
        background: $primary;
        font-size: 16px;
        color: $white; }

    &__product-rating {
        display: flex;
        gap: 30px;
        width: 100%;
        padding: 30px;
        border: 1px solid $black;
        border-radius: 10px;
        font-size: 18px;
        line-height: 110%;

        .left {
            flex-shrink: 0;
            width: 130px;

            h2 {
                margin-bottom: 20px;
                font-weight: 700;
                font-size: 50px; } }

        .right {
            width: 100%; } }

    &__rating-row {
        display: flex;
        align-items: center;
        gap: 10px;
        margin-bottom: 10px;

        span {
            width: 10px; } }

    &__rating-indicator {
        position: relative;
        width: 160px;
        height: 3px;
        border-radius: 10px;
        background: $gray;

        &--full {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            background: $primary; } } }


.review {
    position: relative;
    display: flex;
    gap: 30px;

    &__avatar {
        flex-shrink: 0;
        width: 65px;
        height: 65px;
        border-radius: 50%;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%; } }

    &__content {
        width: 100%; }

    &__header {
        display: flex;
        gap: 15px;
        align-items: flex-start;
        margin-bottom: 10px;

        h3 {
            font-size: 18px;
            line-height: 110%;
            font-weight: bold; } }

    &__vote {
        display: inline-block;
        margin-bottom: 2px; }

    &__text {
        margin-bottom: 20px;
        line-height: 120%; }

    &__footer {
        display: flex;
        gap: 20px; }

    &__voting-btn {
        gap: 5px;
        color: $black;
        font-size: 18px;

        svg {
            width: 22px;
            height: 21px; } }

    &__like {
        align-self: flex-start;

        svg {
            margin-top: -10px; } }

    &__images {
        display: flex;
        gap: 5px;
        margin-bottom: 20px;

        img {
            width: 100px;
            height: 120px;
            border-radius: 5px; } } }






@include media-breakpoint-down(xl);


@include media-breakpoint-only(lg) {
    .reviews {
        &__rating-indicator {
            width: 108px; } } }

@include media-breakpoint-down(lg) {
    .reviews {
        flex-direction: column-reverse;
        gap: 40px;

        &__product-rating {
            width: 390px;
            height: 210px; }

        &__rating-indicator {
            width: 129px; } } }

@include media-breakpoint-only(md) {
    .reviews {
        &__add-button {
            position: absolute;
            top: 252px;
            right: 0; } } }

@include media-breakpoint-down(md) {
    .reviews {
        flex-direction: column-reverse;
        gap: 40px;

        & > .right {
            flex-direction: column-reverse;
            gap: 20px; }

        &__add-button {
            width: 100%; }

        &__product-rating {
            gap: 0;
            width: 100%; } }

    .review {
        &__avatar {
            position: absolute;
            top: 0;
            left: 0;
            width: 50px;
            height: 50px; }

        &__header {
            flex-direction: column;
            gap: 10px;
            margin-left: 65px; }

        &__images {
            img {
                width: 116px;
                height: 130px; } } } }

