.shopping-cart {
    &__list {
        display: flex;
        flex-direction: column;
        width: 100%;
        border-bottom: 1px solid #E9E9E9; }

    &__item {
        position: relative;
        display: flex;
        gap: 30px;
        padding-block: 30px;
        border-top: 1px solid #E9E9E9;

        img {
            width: 110px;
            height: 100px;
            border-radius: 10px; }

        .btn {
            width: 35px;
            height: 35px;
            background: #F4F4F4;
            outline: 1px solid $gray;

            &:hover,
            &:active {
                outline: 1px solid $gray !important; } }


        &-info {
            display: flex;

            & > .d-flex:first-child {
                justify-content: space-between; }

            & > .d-flex:last-child {
                gap: 5px; } }

        &-title {
            h3 {
                font-size: 22px;
                line-height: 120%;
                font-weight: bold; } }

        &-amount-group {
            gap: 1px;
            padding: 1px;

            span {
                font-size: 22px;
                margin-top: 5px; }

            .shopping-cart__item-amount-decrease {
                border-radius: 5px 0px 0px 5px;

                .icon {
                    width: 7px; } }

            .shopping-cart__item-amount-increase {
                border-radius: 0px 5px 5px 0px;

                .icon {
                    width: 10px; } } }

        &-price {
            font-size: 22px;
            line-height: 120%;
            font-weight: bold; }

        &-old-price {
            font-weight: 400;
            font-size: 16px;
            line-height: 100%;
            text-decoration-line: line-through;
            color: #A2A2A2; }

        &-remove-btn {
            position: absolute;
            top: 30px;
            right: 0;
            border-radius: 5px;
            color: #8C8C8C;

            .icon {
                width: 16px;
                height: 16px; } } } }

@include media-breakpoint-only(lg) {
    .shopping-cart {
        &__item {
            &-info {
                gap: 60px;

                & > .d-flex:first-child {
                    width: 335px; } } } } }

@include media-breakpoint-down(xl) {
    .shopping-cart {
        &__item {
            &-info {
                gap: 30px; } } } }

@include media-breakpoint-only(lg) {
    .shopping-cart {
        &__item {
            &-info {
                & > .d-flex:first-child {
                    width: 230px; } } } } }

@include media-breakpoint-only(md) {
    .shopping-cart {
        &__item {
            &-info {
                & > .d-flex:first-child {
                    width: 310px; } } } } }

@include media-breakpoint-down(sm) {
    .shopping-cart {
        &__item {
            flex-direction: column;
            gap: 15px;

            &-info {
                & > .d-flex:first-child {
                    gap: 30px;
                    width: 225px; } } } } }
