//header top
.header {
    &__top {
        border-bottom: 1px solid $gray;
        font-size: 15px;

        .header {
            &__badges {
                width: 298px; }

            &__community {
                gap: 5px;

                .community-link {
                    width: 24.33px;
                    height: 24.33px;
                    background: $black;
                    color: $white; } }

            &__auth {
                gap: 5px;
                color: $gray;

                .icon {
                    width: 20px;
                    height: 23px; } } }


        .container {
            justify-content: space-between;
            align-items: center;
            width: 100%; }

        .right {
            gap: 32px; } }

    &__top-nav {
        gap: 15.90px; }

    &__nav-link {
        color: $gray; } }


//user-location
.user-location {
    gap: 10px;
    color: $black;

    .icon {
        margin-top: 3px; } }

//end user-location


@include media-breakpoint-up(xl) {
    .header {
        &__top {
            .container {
                height: 54px; } } } }

@include media-breakpoint-down(xl) {
    .header {
        &__top {
            .container {
                height: 50px; } }

        &__top-nav {
            display: none !important; } } }

@include media-breakpoint-down(lg) {
    .header {
        &__top {
            .header__badges {
                display: none !important; } } } }

@include media-breakpoint-down(md) {
    .header {
        &__top {
            .header__community {
                display: none !important; } } } }
