.youtube-channel {
    .section__content {
        display: flex;
        gap: 30px; } }

.youtube-video {
    &__list {
        display: flex;
        flex-direction: column;
        gap: 20px; }

    &__preview {
        &--big {
            height: 400px;

            & .preview__title {
                height: 149px; } }

        &--small {
            height: 190px;

            & .preview__title {
                height: 100px; } } } }

@include media-breakpoint-up(xl) {
    .youtube-video {
        &__preview {
            &--big {
                width: 730px; }

            &--small {
                width: 350px; } } } }

@include media-breakpoint-only(lg) {
    .youtube-video {
        &__preview {
            &--big {
                width: 610px; }

            &--small {
                width: 290px; } } } }

@include media-breakpoint-down(lg) {
    .youtube-channel {
        .section__content {
            flex-direction: column; } }

    .youtube-video {
        &__preview {
            &--big {
                width: 100%; } } } }


@include media-breakpoint-only(md) {
    .youtube-video {
        &__preview {
            &--small {
                width: 330px;
                height: 228px; } }
        &__list {
            flex-direction: row;
            gap: 30px; } } }

@include media-breakpoint-down(md) {
    .youtube-video {
        &__preview {
            &--small {
                width: 100%; } } } }

@include media-breakpoint-only(sm) {
    .youtube-channel {
        .section__content {
            gap: 20px; } }

    .youtube-video {
        &__preview {
            &--big {
                height: 300px;

                & .preview__title {
                    height: 149px; } }

            &--small {
                height: 300px;

                & .preview__title {
                    height: 149px; } } } } }

@include media-breakpoint-down(sm) {
    .youtube-channel {
        .section__heading {
            h3 {
                margin-bottom: -3px; } } }

    .youtube-channel {
        .section__content {
            gap: 20px; } }

    .youtube-video {
        &__preview {
            height: 240px;

            .preview__title {
                height: 90px; } } } }
