.filter-panel {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
    width: 255px;
    height: fit-content;
    padding-block: 30px;
    background: $white;
    color: $black;
    border: 1px solid #D9D9D9;
    border-radius: 10px;

    &__heading {
        display: none; }

    &__option {
        width: 208px;
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid $gray;

        h3 {
            min-width: 213px;
            margin-bottom: 20px;
            font-size: 19px;
            line-height: 19px;
            text-transform: uppercase; }

        &--price {
            .filter-panel__dual-range-field {
                &::after {
                    content: ''; } } }

        &--volume {
            .filter-panel__dual-range-field {
                &::after {
                    content: ''; } } }

        &--weight {
            .filter-panel__dual-range-field {
                &::after {
                    content: ''; } } } }


    &__dual-range {
        &-slider {
            position: relative;
            width: 100%;
            height: 20px;
            padding-top: 8px;
            margin-bottom: 13px;

            input {
                position: absolute;
                appearance: none;
                width: 100%;
                height: 5px;
                border: 0;
                border-radius: 5px;
                outline: none;
                pointer-events: none;

                &:active,
                &:focus {
                    outline: none; }

                &::-webkit-slider-thumb {
                    height: 20px;
                    width: 20px;
                    border: 2px solid $primary;
                    border-radius: 50%;
                    background-color: $white;
                    position: relative;
                    margin: 0;
                    cursor: pointer;
                    appearance: none;
                    pointer-events: all;
                    z-index: 2; }

                &::-moz-range-thumb {
                    height: 20px;
                    width: 20px;
                    border: 2px solid $primary;
                    border-radius: 50%;
                    background-color: $white;
                    position: relative;
                    margin: 0;
                    cursor: pointer;
                    appearance: none;
                    pointer-events: all;
                    z-index: 2; }

                &:nth-child(1) {
                    background: none;
                    background-size: 100% 5px;
                    z-index: 1; }

                &:nth-child(2) {
                    background: linear-gradient(to right, $primary 0%, $primary 100%, $gray 100%, $gray 100%);
                    background-size: 100% 5px; } } }

        &-fields {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            input {
                width: 75px;
                height: 35px;
                padding-inline: 10px 15px;
                border: 1px solid $gray;
                border-radius: 5px;
                font-size: 14px;
                line-height: 14px; } }



        &-field {
            position: relative;
            width: fit-content;

            &::after {
                    position: absolute;
                    display: inline-block;
                    top: 6px;
                    right: 8px;
                    z-index: 2; } } }

    &__field-divider {
        width: 28px;
        height: 1px;
        background: $gray; }

    &__checkbox-group {
        display: flex;
        flex-direction: column;
        gap: 10px;

        label {
            display: block;
            position: relative;
            padding-left: 30px;
            cursor: pointer;
            font-size: 14px;
            line-height: 19.7px;
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;

            input {
                position: absolute;
                opacity: 0;
                cursor: pointer;
                height: 0;
                width: 0; }

            span {
                position: absolute;
                top: 0;
                left: 0;
                height: 20px;
                width: 20px;
                background: #FFFFFF;
                border: 1px solid #000000;
                border-radius: 5px;

                &:after {
                    content: "";
                    position: absolute;
                    display: none;
                    left: 4px;
                    top: 4px;
                    width: 10px;
                    height: 9px;
                    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.757812 4.06879L3.31307 6.49935L7.42448 0.666016' stroke='white' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                    background-position: center;
                    background-repeat: no-repeat; } }

            &:hover input ~ .checkmark {
                background-color: #FFFFFF; }

            & input:checked {
                & ~ .checkmark {
                    background-color: #EB8815;
                    border: 1px solid #EB8815;

                    &:after {
                        display: block; } } } } } }

@include media-breakpoint-down(xl) {
    .filter-panel {
        display: none; } }
