.about-shop {
    .section__content {
        display: flex;
        flex-direction: column;
        gap: 40px; }

    .splide {
        &__arrow {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            background: transparent;
            border-radius: 50%;
            outline: none !important;
            fill: $black;

            &:disabled {
                border-color: $gray !important;

                svg {
                    fill: $gray !important; } }

            &--prev {
                svg {
                    margin-right: 4px; } }
            &--next {
                svg {
                    margin-left: 4px;
                    transform: scale(-1, 1); } } } } }

.video-slide {
    display: flex;
    gap: 30px;

    &__preview {
        flex-shrink: 0; }

    &__description {
        display: flex;
        flex-direction: column;
        gap: 30px;

        .text {
            width: 100%;
            line-height: 120%;

            p:not(:last-child()) {
                margin-bottom: 20px; } }

        .stats {
            display: flex;
            column-gap: 30px; } } }

.stats__item {
    width: 160px;
    height: 64px;

    &-text {
        font-weight: bold;
        font-size: 40px;
        line-height: 100%; }

    &-sub-text {
        font-size: 16px;
        line-height: 120%; } }

#about-shop-thumbs {
    position: relative;

    & .splide__slide {

        &.is-active {
            border: none !important; }

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 10px; } } }

@include media-breakpoint-up(xl) {
    .video-slide {
        &__preview {
            width: 540px;
            height: 360px; }

        &__description {
            .text {
                height: 228px; } } }

    #about-shop-thumbs {

        .splide {
            &__slide {
                height: 190px; }

            &__arrow {
                    top: 50%;
                    transform: translateY(-50%);
                    height: 50px;
                    width: 50px;
                    border: 3px solid $black;

                    &--prev {
                        left: -100px; }

                    &--next {
                        right: -100px; }

                    svg {
                        width: 16px;
                        height: 23px; } } } } }

@media screen AND ( max-width: 1365px ) {
    .about-shop {
        .section__content {
            gap: 70px; } }

    #about-shop-thumbs {
        .splide {
            &__arrow {
                    top: -70px;
                    height: 40px;
                    width: 40px;
                    border: 2px solid $black;

                    &--prev {
                        right: 60px;
                        left: unset; }

                    &--next {
                        right: 0; }

                    svg {
                        width: 12px;
                        height: 18px; } } } } }

@media screen AND ( max-width: 1365.8px ) AND ( min-width: 1200px ) {
    #about-shop-thumbs {
        .splide {
            &__arrow {
                top: -50px;
                height: 50px;
                width: 50px;

                &--prev {
                    right: 70px; }

                svg {
                    width: 16px;
                    height: 23px; } } } } }


@include media-breakpoint-only(lg) {
    .video-slide {
        &__preview {
            width: 370px;
            height: 360px; }

        &__description {
            .text {
                width: 530px;
                height: 247px;
                line-height: 118%; }

            .stats {
                column-gap: 0; } } }

    #about-shop-thumbs {

        .splide {
            &__slide {
                height: 200px; } } } }

@include media-breakpoint-down(lg) {
    .video-slide {
        flex-direction: column;

        &__preview {
            width: 100%; }

        &__description {
            width: 100%;

            .stats {
                &__item {
                    height: 59px; } } } } }

@include media-breakpoint-only(md) {
    .video-slide {
        &__preview {
            height: 360px; }

        &__description {
            .text {
                line-height: 116%; }

            .stats {
                column-gap: 20px; } } }

    #about-shop-thumbs {

        .splide {
            &__slide {
                height: 228px; } } } }

@include media-breakpoint-only(sm) {
    .video-slide {
        &__preview {
            height: 280px; }

        &__description {
            .stats {
                column-gap: 20px; } } }

    #about-shop-thumbs {

        .splide {
            &__slide {
                height: 185px; }

            &__arrow {
                top: -60px; } } } }

@include media-breakpoint-down(sm) {
    .about-shop {
        .section__content {
            gap: 55px; } }

    .video-slide {
        &__preview {
            height: 240px; }

        &__description {
            .text {
                line-height: 118%; }

            .stats {
                flex-wrap: wrap;
                row-gap: 20px;
                column-gap: 35px; } } }

    #about-shop-thumbs {

        .splide {
            &__slide {
                height: 240px; }

            &__arrow {
                top: -60px;

                &--prev {
                    right: 50px; } } } } }
