.advantages {
    margin-top: 50px;
    gap: 30px;

    &__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 15px; }

    &__icon {
        height: 50px;
        width: 50px;
        box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
        color: #D57300;
        border-radius: 50%; }

    &__text {
        line-height: 112%;
        font-size: 16px;
        color: $black;

        & a {
            color: $black;
            text-decoration: underline; } }

    .icon {
        height: 29.17px;
        width: 29.17px; } }


@include media-breakpoint-up(xl) {
    .advantages {
        &__text {
            width: 190px; } } }

@include media-breakpoint-only(lg) {
    .advantages {

        &__item {
            align-items: flex-start; }

        &__text {
            width: 145px; } } }

@include media-breakpoint-only(md) {
    .advantages {
        flex-wrap: wrap;

        &__text {
            width: 265px; } } }

@include media-breakpoint-only(sm) {
    .advantages {
        flex-wrap: wrap;

        &__text {
            width: 175px; } } }

@include media-breakpoint-down(sm) {
    .advantages {
        flex-direction: column;
        gap: 20px;

        &__text {
            width: 265px; } } }
