.order-forming {
    height: 429px;

    .section__content {
        position: absolute;
        overflow: visible !important; }

    &__stage {
        position: relative;
        border: none !important;
        outline: none !important;
        cursor: default !important;

        img {
            border-radius: 10px;
            width: 240px;
            height: 180px; }

        P {
            width: 100%;
            margin: 12px 0 0 0;
            font-size: 15px;
            line-height: 128%;
            letter-spacing: 0.5px; }

        .circle-icon {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            top: 70px;
            right: 0px;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            font-size: 24px;
            color: $white;
            font-weight: bold;
            background: linear-gradient(93.46deg, #FF7A00 0%, #FF8A00 100%);
            box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25); }

        .dashed-line {
            position: absolute;
            top: 89px;
            left: -30px;
            width: 30px;
            height: 2px;
            border-top: 2px dashed #FF7A00; } } }

@include media-breakpoint-up(lg) {
    .order-forming {
        height: 429px; } }

@include media-breakpoint-down(lg) {
    .order-forming {
        height: 495px; } }

@include media-breakpoint-down(sm) {
    .order-forming {
        height: 561px; } }
