.page {
    margin: 0 auto;
    text-align: left;
    min-width: 320px;
    width: 100%;
    transition: margin .2s ease;
    position: relative;
    min-height: 100vh;
    overflow-x: hidden;

    // Fix for IE11
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        display: block; }

    &__main {
        margin-top: 50px; }

    &__title {
        margin-bottom: 20px;

        h1 {

            font-size: 40px;
            font-weight: bold;
            line-height: 100%;
            letter-spacing: 0.02rem; } } }
