/* Include fonts
   @mixin ======================================================================== */
@font-face {
	font-family: 'Circe';
	src: url('../fonts/Circe-Thin.eot');
	src: local('Circe Thin'), local('Circe-Thin');
	src: url('../fonts/Circe-Thin.eot?#iefix') format('embedded-opentype');
	src: url('../fonts/Circe-Thin.woff') format('woff');
	src: url('../fonts/Circe-Thin.ttf') format('truetype');
	font-weight: 100;
	font-style: normal; }

