@import './top/top';
@import './main/main';
@import './bottom/bottom';

.footer {
    width: 100%;
    margin-top: 100px;
    padding: 61px 0 30px 0;
    background: $black;

    &__logo {
        color: $white;
        width: 170px;
        height: 47px; } }

@include media-breakpoint-down(sm) {
    .footer {
        margin-top: 98px;
        padding-bottom: 18px;

        &__logo {
            width: 165px;
            height: 45px; } } }
