#product-card {
    position: relative;
    justify-content: flex-end; }

#card-images {
    height: fit-content;
    transform: translateZ(0);
    -webkit-transform: translateZ(0);

    &[fixed] {
        position: fixed;
        top: 0; }

    &[absolute] {
        position: absolute;
        bottom: 0;
        left: 0; } }

.product-page {
    &__container {
        display: flex;
        gap: 30px;

        & > .left {
            display: flex;
            flex-shrink: 0;
            gap: 13px;
            width: 635px; }

        & > .right {
            width: 445px; } }

    &__thumbs {
        flex-shrink: 0;
        width: 80px;

        .splide__slide {
            border-radius: 10px;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover; }

            &.is-active {
                border: 1px solid #000000; } } }

    &__image {
        width: 100%;

        .splide {
            &__slide {
                border-radius: 10px;
                overflow: hidden;

                img {
                    width: 100%;
                    object-fit: cover; } }

            &__pagination {
                position: absolute;
                gap: 10px;
                bottom: 20px;
                left: 50%;
                transform: translateX(-50%);

                &__page {
                    width: 10px;
                    height: 10px;
                    border: 1px solid $black;
                    border-radius: 50%;
                    background: transparent;

                    &.is-active {
                        width: 15px;
                        height: 15px;
                        background: $black; } } } } }

    &__info {
        width: 100%;

        &-bottom {
            width: 100%; } }

    &__tag {
        width: 100%;
        margin-bottom: 20px;
        font-size: 18px;
        color: $white;

        &-discount,
        &-new {
            display: inline-block;
            padding: 5px 10px;
            line-height: 27px;
            border-radius: 4px;
            text-align: center; }

        &-discount {
            margin-right: 8px;
            background: $primary; }

        &-new {
            background: $success; } }

    &__top-row {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
        font-size: 18px;
        line-height: 110%; }

    &__reviews {
        display: flex;

        &-text {
            margin-right: 10px;
            text-decoration-line: underline; } }

    &__rating-point {
        margin-left: 5px; }

    &__vendor-code {
        color: gray; }

    h1 {
        width: 100%;
        max-height: 80px;
        margin-bottom: 10px;
        line-height: 100%;
        font-size: 40px;
        font-weight: bold;
        overflow: hidden; }

    &__in-stock {
        font-size: 24px;
        line-height: 110%;
        margin-bottom: 30px; }

    &__price {
        display: flex;
        align-items: flex-end;
        gap: 20px;
        margin-bottom: 10px;

        .price {
            font-size: 32px;
            line-height: 130%;
            font-weight: bold; }


        .old-price {
            font-size: 24px;
            line-height: 150%;
            text-decoration: line-through; }

        .benefit {
            font-size: 24px;
            line-height: 150%;
            color: $primary; } }

    .link-secondary {
        display: inline-block;
        width: 100%;
        margin-bottom: 30px;
        font-size: 18px;
        line-height: 130%;
        text-decoration: underline; }

    &__installment {
        display: flex;
        align-items: center;
        gap: 10px;
        margin-bottom: 20px;
        color: #00A469;

        &-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 25px;
            width: 25px;
            background: #00A469;
            color: #fff;

            .icon {
                width: 15px;
                height: 15px; } }

        &-text {
            font-size: 24px;
            line-height: 130%; } }

    &__row {
        display: flex;
        align-items: center;
        gap: 30px;
        margin-bottom: 10px; }

    &__add-to-cart {
        flex-shrink: 0;
        width: 347px;
        height: 69px;
        background: #000;
        border-radius: 5px;
        font-size: 24px;
        color: $white;
        font-weight: bold; }

    &__like {
        display: flex;
        align-items: center;
        gap: 5px;
        margin-right: 15px;
        font-size: 24px;
        color: $gray;

        & .icon {
            width: 22px;
            height: 22px;
            margin-top: -5px; } }

    &__one-click-buy {
        width: 100%;
        height: 69px;
        margin-bottom: 30px;
        border: 1px solid #000;
        border-radius: 5px;
        font-size: 24px;
        background: #fff; }

    .accordion {
        &-item:not(:last-child) {
            margin-bottom: 30px; }

        &-header {
            margin-bottom: 5px; }

        &-button {
            font-weight: 700;
            font-size: 32px; }

        &-collapse {
            display: none;

            &.show {
                display: block; } }

        &-body {
            line-height: 120%; } } }

@include media-breakpoint-only(lg) {
    .product-page {

        &__container {
            & > .left {
                width: 530px; }

            & > .right {
                width: 370px; } }

        &__top-row {
            flex-direction: column-reverse;
            gap: 13px; }

        &__installment {
            align-items: flex-start;

            &-text {
                br {
                    display: block !important; } } }

        &__row {
            gap: 30px; }

        &__add-to-cart {
            width: 290px; } } }

@include media-breakpoint-down(lg) {
    .product-page {

        &__container {
            flex-direction: column;
            gap: 20px;

            & > .left {
                width: 100%; }

            & > .right {
                width: 100%; } }

        &__thumbs {
            display: none; } } }


@include media-breakpoint-only(md) {
    .product-page {
        &__image {
            .splide {
                &__slide {
                    img {
                        height: 437px; } } } }

        &__add-to-cart {
            width: 360px; }

        &__one-click-buy {
            width: 450px; } } }

@include media-breakpoint-only(sm) {
    .product-page {
        &__add-to-cart {
            width: 428px; } } }

@include media-breakpoint-down(sm) {
    .product-page {
        &__image {
            flex-shrink: 0;
            width: 100vw;
            margin-left: calc( ( 100vw - 360px ) / -2 ); }

        &__top-row {
            flex-direction: column-reverse;
            gap: 13px; }

        h1 {
            margin-bottom: 20px; }

        &__price {
            gap: 15px; }

        &__installment {
            align-items: flex-start;

            &-text {
                br {
                    display: block !important; } } }

        &__row {
            gap: 20px; }

        .accordion {
            &-button {
                font-size: 28px; } } } }
