// Font
$font-family-base: 'Circe', sans-serif;
$enable-smooth-scroll: false;

/**
 * Grid system
 * -----------------------------------------
 */

// Grid breakpoints
$grid-breakpoints: (
	xs: 	0,
	sm: 	576px,
	md: 	768px,
	lg: 	992px,
	xl: 	1200px,
);

// Grid containers
$container-max-widths: (
	xs: 360px,
	sm: 510px,
	md: 690px,
	lg: 930px,
	xl: 1110px,
);

// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-columns:                12;
$grid-gutter-width:           0;
$grid-row-columns:            6;

// Links
//$link-color: $blue;
//$link-decoration:                         none;
//$link-hover-color:                        darken($link-color, 15%);
//$link-hover-decoration:                   underline;
// Darken percentage for links with `.text-*` class (e.g. `.text-success`)
//$emphasized-link-hover-darken-percentage: 15%;


/**
 * Spacing
 * ---------------------------------------------
 */
$spacer: 1rem;
$spacers: (
	0: 0,
	1: ($spacer * .25),
	2: ($spacer * .5),
	3: $spacer,
	4: ($spacer * 1.5),
	5: ($spacer * 3),
	10: ($spacer * 0.625), // 10px
	15: ($spacer * 0.9375), // 15px
	20: ($spacer * 1.25), // 20px
	25: ($spacer * 1.5625), // 25px
	30: ($spacer * 1.875), // 30px
	40: ($spacer * 2.5), // 40px
	50: ($spacer * 3.125), // 50px
	60: ($spacer * 3.75), // 60px
	70: ($spacer * 4.375), // 70px
	80: ($spacer * 5), // 80px
	90: ($spacer * 5.625), // 90px
	100: ($spacer * 6.25) // 100px
);

// $negative-spacers: if($enable-negative-margins, negativify-map($spacers), null) !default;

/*
 * Color system
 * ---------------------------------------------
 */

$white:    #fff;
$gray-100: #f8f9fa;
$gray-200: #f4f4f4;
$gray-300: #dee2e6;
$gray-400: #e4e4e4;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;
$black:    #000;

// fusv-disable
$grays: (
	"100": $gray-100,
	"200": $gray-200,
	"300": $gray-300,
	"400": $gray-400,
	"500": $gray-500,
	"600": $gray-600,
	"700": $gray-700,
	"800": $gray-800,
	"900": $gray-900
);
// fusv-enable

$gray:	  #B4B4B4;
$blue:    #0d6efd;
$indigo:  #6610f2;
$purple:  #6f42c1;
$pink:    #d63384;
$red:     #E40000;
$orange:  #EB8815;
$yellow:  #ffc107;
$green:   #00A469;
$teal:    #20c997;
$cyan:    #0dcaf0;

// scss-docs-start colors-map
$colors: (
	"blue":       $blue,
	"indigo":     $indigo,
	"purple":     $purple,
	"pink":       $pink,
	"red":        $red,
	"orange":     $orange,
	"yellow":     $yellow,
	"green":      $green,
	"teal":       $teal,
	"cyan":       $cyan,
	"white":      $white,
	"gray":       $gray,
	"gray-dark":  $gray-800
);
// scss-docs-end colors-map

$primary:       $orange;
$secondary:     $gray;
$success:       $green;
$info:          $cyan;
$warning:       $yellow;
$danger:        $red;
$light:         $gray-100;
$dark:          $gray-900;

// Links
$link-primary:	$gray;

// scss-docs-start theme-colors-map
$theme-colors: (
	"primary":    $primary,
	"secondary":  $secondary,
	"success":    $success,
	"info":       $info,
	"warning":    $warning,
	"danger":     $danger,
	"light":      $light,
	"dark":       $dark
);

// breadcrumbs

$breadcrumb-font-size:              14px;
$breadcrumb-padding-y:              0;
$breadcrumb-padding-x:              0;
$breadcrumb-item-padding-x:         .6rem;
$breadcrumb-margin-bottom:          20px;
$breadcrumb-bg:                     null;
$breadcrumb-divider-color:          $gray;
$breadcrumb-active-color:           $black;
$breadcrumb-divider:                quote("/");
$breadcrumb-divider-flipped:        $breadcrumb-divider;
$breadcrumb-border-radius:          null;


// Forms
$input-group-addon-bg:                  transparent;


//Accordion
$accordion-padding-y:                     0;
$accordion-padding-x:                     0;
$accordion-color:                         $black;
$accordion-bg:                            $white;
$accordion-border-width:                  0;
$accordion-border-color:                  none;
$accordion-border-radius:                 0;
$accordion-inner-border-radius:           0;

$accordion-body-padding-y:                0;
$accordion-body-padding-x:                0;

$accordion-button-padding-y:              $accordion-padding-y;
$accordion-button-padding-x:              $accordion-padding-x;
$accordion-button-color:                  $accordion-color;
$accordion-button-bg:                     $white;
$accordion-transition:                    0;
$accordion-button-active-bg:              $white;
$accordion-button-active-color:           $black;

$accordion-button-focus-border-color:     transparent;
$accordion-button-focus-box-shadow:       none;

$accordion-icon-width:                    22px;
$accordion-icon-color:                    $accordion-button-color;
$accordion-icon-active-color:             $accordion-button-active-color;
$accordion-icon-transition:               transform .2s ease-in-out;
$accordion-icon-transform:                none !important;

$accordion-button-icon:         		url("data:image/svg+xml,%3Csvg width='22' height='13' viewBox='0 0 22 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21 11.5L11 1.5L1 11.5' stroke='black' stroke-width='2'/%3E%3C/svg%3E%0A");
$accordion-button-active-icon:         	url("data:image/svg+xml,%3Csvg width='22' height='13' viewBox='0 0 22 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21 11.5L11 1.5L1 11.5' stroke='black' stroke-width='2'/%3E%3C/svg%3E%0A");

.accordion-button::after {
	margin-top: 7px;
	transform: rotate(-180deg);
}