body {
    font-family: $font-family-base; }

a {
    text-decoration: none;

    &:hover {
        color: #777; } }

p {
    margin: 0; }

input {
    &:active,
    &:hover,
    &:focus, {
        outline: none; } }

textarea {
    &:active,
    &:hover,
    &:focus {
        outline: none; } }


.icon-btn {
    background: transparent; }

button,
.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    border: none;
    outline: none; }

.breadcrumb a {
    color: $gray; }




//circle
.circle {
    border-radius: 50%; }

.d-flex-center {
    display: flex;
    justify-content: center;
    align-items: center; }

.section {
    margin-top: 80px;

    &__heading {
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;
        gap: 10px;

        .top {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            flex-wrap: wrap; }

        h2 {
            font-size: 40px;
            font-weight: bold;
            line-height: 100%; }

        h3 {
            font-size: 24px;
            font-weight: 400;
            line-height: 110%; } }

    &__head-link {
        margin-block: 10px -5px;

        a {
            font-size: 18px;
            line-height: 100%;
            text-decoration: underline;
            color: $primary; } } }

.contacts {
    display: flex;
    gap: 30px;

    &__item {
        display: flex;
        flex-direction: column;
        gap: 10px; }

    &__text-link {
        text-decoration: underline; } }


.preview {
    position: relative;

    &__img {
        width: 100%;
        height: 100%;
        border-radius: 10px;
        object-fit: cover; }

    &__title {
        position: absolute;
        display: flex;
        align-items: flex-end;
        left: 0;
        bottom: 0;
        width: 100%;
        padding: 0 20px 20px 20px;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
        border-radius: 0px 0px 10px 10px;
        font-size: 14px;
        color: $white;
        line-height: 110%;

        & span {
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap; } }


    &__play-button {
        position: absolute;
        left: 50%;
        top: 50%;
        width: 60px;
        height: 60px;
        transform: translate(-50%, -50%);
        color: #fff;
        background: $red;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
        cursor: pointer;

        .icon {
            width: 25px;
            height: 25px;
            margin-left: 7px; } } }

//community icon sizes
.whatsapp--tiny {
    width: 13px; }

.youtube--tiny {
    width: 13px; }

.instagram--tiny {
    width: 11px;
    margin-bottom: 1px; }

.vk--tiny {
    width: 15px; }

.tiktok--tiny {
    width: 12px; }

.user-auth--small {
    width: 20px;
    height: 20px; }


.whatsapp--small {
    width: 16px; }

.youtube--small {
    width: 16px; }

.instagram--small {
    width: 14px;
    margin-bottom: 1px; }

.vk--small {
    width: 17px; }

.tiktok--small {
    width: 15px;
    margin-left: 1px; }

.whatsapp--bg-color {
    background: #58BE55; }

.youtube--bg-color {
    background: #EA3323; }

.vk--bg-color {
    background:  #4585ED; }
.tiktok--bg-color {
    background: #FFFFFF; }
//end community icon sizes

.product-tag {
    display: flex;
    justify-content: center;
    gap: 10px;
    color: #fff;

    &__discount {
        display: flex;
        background: $primary;
        border-radius: 4px; }

    &__new {
        background: $green;
        text-align: center;
        border-radius: 4px; } }

.rating-stars {
    position: relative;
    width: 91px;
    height: 16px;

    svg {
        vertical-align: top;
        width: 91px;
        height: 16px; }

    &--empty,
    &--full {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 0; }

    &--empty {
        width: 100%;
        color: $gray;
        z-index: 1; }

    &--full {
        width: 0;
        color: #FCC917;
        overflow: hidden;
        z-index: 2; } }


.agreement {
    position: relative;
    padding-left: 32px;
    max-width: 395px;
    margin-top: 10px;
    line-height: 100%;

    span {
        font-size: 14px;
        color: $black; }

    input {
        display: none; }

    a {
        font-size: 14px;
        color: $black;
        text-decoration: underline; }

    .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        height: 20px;
        width: 20px;
        background: #FFFFFF;
        border: 1px solid #000000;
        border-radius: 5px;
        cursor: pointer;

        &:after {
            content: "";
            position: absolute;
            display: none;
            left: 4px;
            top: 4px;
            width: 10px;
            height: 9px;
            background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.757812 4.06879L3.31307 6.49935L7.42448 0.666016' stroke='white' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
            background-position: center;
            background-repeat: no-repeat; } }

    &:hover input ~ .checkmark {
        background-color: #FFFFFF; }

    & input:checked {
        & ~ .checkmark {
            background-color: #EB8815;
            border: 1px solid #EB8815;

            &:after {
                display: block; } } } }
