.footer__bottom {
    display: flex;
    align-items: center;
    margin-top: 60px; }

.footer__bottom,
.footer__bottom a {
    color: $gray;
    font-size: 14px; }

.privacy-policy {
    margin-left: auto; }

@include media-breakpoint-up(xl) {
    .copyright {
        width: 285px; } }

@include media-breakpoint-down(xl) {
    .footer__bottom {
        margin-top: 58px; } }

@include media-breakpoint-only(lg) {
    .copyright {
        width: 240px; } }

@include media-breakpoint-only(md) {
    .copyright {
        width: 180px; } }

@include media-breakpoint-down(md) {
    .footer__bottom {
        flex-direction: column;
        justify-content: center;
        gap: 16px;
        margin-top: 56px; }

    .privacy-policy {
        margin: 0; } }
