.footer {
    &__main {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        gap: 30px;
        margin-top: 40px; }

    &__column {
        min-width: 160px;

        &-header {
            height: 30px;
            line-height: 100%;
            margin-bottom: 5px;
            font-size: 16px;
            font-weight: bold;
            color: $gray;
            letter-spacing: 0.05em;
            text-transform: uppercase; }

        &-link {}

        &-link a {
            line-height: 179%;
            font-size: 14px;
            color: $white; } }

    &__logo-container {
        height: 47px;

        & a {
            color: #fff; } }

    &__community {
        &-container {
            display: flex;
            gap: 7.5px; }

        &-link {
            width: 30px;
            height: 30px;

            a {
                width: 100%;
                height: 100%;
                color: #fff; } }

        .whatsapp {
           background: #58BE55; }

        .youtube {
            background: #EA3323; }

        .instagram {
            background: linear-gradient(225deg, #BA44A4 0%, #EC624A 51.04%, #F9DC6C 100%); }

        .vk {
            background: #4585ED; }

        .tiktok {
            background: $white;
            a {
                color: $black; } } } }

.organization-info {
    font-size: 14px;
    color: $gray;
    line-height: 150%;
    margin-top: 19px; }

@include media-breakpoint-up(xl) {
    .footer {
        &__column {
            &:first-child {
                width: 255px; } } } }

@include media-breakpoint-only(lg) {
    .footer {
        &__column {
            &:first-child {
                width: 210px; } }

        &__community {
            margin-left: 20px; } } }

@include media-breakpoint-only(md) {
    .footer {
        &__column {
            &:first-child {
                width: 210px; } }

        .organization-info {
            padding-right: 15px; }

        &__community {
            position: absolute;
            top: 190px;
            left: 0; } } }

@include media-breakpoint-only(sm) {
    .footer {
        &__column {
            &:first-child {
                width: 240px; } }

        &__community {
            margin-left: 80px; } } }

@include media-breakpoint-down(sm) {
    .footer {
        &__main {
            row-gap: 40px; }
        &__column {
            &:first-child {
                width: 165px; } }

        &__community {
            margin-left: 5px; } } }
