.auth {
    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 350px; }

    &__sub-title {
        font-size: 24px;
        line-height: 110%;
        margin-bottom: 20px; }

    &__social {
        &-networks {
            display: flex;
            flex-wrap: wrap;
            gap: 12px 30px;
            margin-bottom: 20px; }

        &-link {
            width: 160px;
            height: 65px;
            border-radius: 5px;
            color: $white;

            &--vk {
                background: #0077FF;

                .icon {
                    width: 35px;
                    height: 22px; } }

            &--ok {
                background: #EE8208;

                .icon {
                    width: 25px;
                    height: 40px; } }

            &--google {
                background: #DB4437;

                .icon {
                    width: 32px;
                    height: 32px; } }

            &--fb {
                background: #0165E1;

                .icon {
                    width: 20px;
                    height: 39px; } } } }

    &__login-form {}

    &__form-field {
        position: relative;
        margin-bottom: 10px;

        label {
            margin-bottom: 5px;
            font-size: 16px;
            line-height: 22px;
            color: $gray; }

        input {
            width: 100%;
            height: 46px;
            padding: 15px;
            border: 1px solid #B4B4B4;
            border-radius: 5px; }

        .btn {
            position: absolute;
            display: none;
            bottom: 15px;
            right: 10px;
            width: 25px;
            height: 19px;
            background: transparent;
            color: $primary;

            .icon-eye-hide {
                width: 25px;
                height: 18.75px; }

            .icon-eye-show {
                width: 25px;
                height: 15.62px; } } }


    &__login-button {
        margin-bottom: 30px;

        .btn {
            width: 350px;
            height: 69px;
            border-radius: 5px;
            font-size: 24px;
            color: $white;
            cursor: pointer;

            &:disabled {
                background: $gray; }

            &:not(:disabled) {
                background: $black; } } }

    &__link {
        font-size: 24px;
        line-height: 110%;
        text-decoration-line: underline;
        color: $black;
        margin-bottom: 20px; }

    .agreement {
        margin-top: 60px; } }



@include media-breakpoint-down(xl);


@include media-breakpoint-down(md);
