.articles {

    &__list {
        flex-wrap: wrap;
        row-gap: 20px;
        column-gap: 30px; }

    &__item {
        display: flex;

        img {
            border-radius: 10px 0px 0px 10px; }

        &-caption {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 15px 15px 8px 15px;
            border-radius: 0px 10px 10px 0px;
            border: 1px solid #DEDEDE; }

        &-title {
            font-weight: 700;
            font-size: 18px;
            line-height: 110%; }

        &-details {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center; }

        &-more a {
            font-size: 14px;
            color: $black;
            line-height: 100%;
            text-decoration: underline; }

        &-date {
            font-size: 14px;
            color: $gray;
            line-height: 100%; } } }

@include media-breakpoint-up(xl) {
    .articles {
        padding-top: 2px;

        &__item {
            img {
                width: 120px;
                height: 140px; }

            &-caption {
                width: 230px; } } } }


@include media-breakpoint-only(lg) {
    .articles {
        &__item {
            img {
                width: 160px;
                height: 140px; }

            &-caption {
                width: 290px; } } } }

@include media-breakpoint-only(md) {
    .articles {
        &__item {
            img {
                width: 160px;
                height: 140px; }

            &-caption {
                width: 530px; } } } }

@include media-breakpoint-only(sm) {
    .articles {
        &__item {
            img {
                width: 140px;
                height: 140px; }

            &-caption {
                width: 370px; } } } }

@include media-breakpoint-down(sm) {
    .articles {
        &__item {
            img {
                width: 110px;
                height: 133px; }

            &-caption {
                width: 250px;
                padding: 15px 15px 11px 15px; } } } }
