.footer__top {
    padding-bottom: 40px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    color: $gray;

    .contacts {
        flex-wrap: wrap;
        color: $white;

        &__item {
            width: 255px;
            flex-grow: 1; }

        &__link {
            font-size: 20px;
            line-height: 100%; }

        &__text {
            font-size: 14px;
            color: $gray;

            &-link {
                color: $gray; } } } }

@include media-breakpoint-down(xl) {
    .footer__top {
        .contacts {
            &__item {
                width: 210px; } } } }
