.shopping-cart__contacts {
    h2 {
        font-size: 32px;
        line-height: 130%;
        font-weight: bold;
        margin-bottom: 11px; }

    &-form {
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-top: 20px; }

    &-row {
        display: flex;
        width: 100%; }

    &-field {
        flex-grow: 1;
        width: 100%;

        &.required {
            label {
                &::after {
                    position: absolute;
                    top: 2px;
                    right: -8px;
                    content: '*';
                    color: $danger; } } } }

    label {
        position: relative;
        line-height: 22px;
        color: $gray;
        margin-bottom: 5px; }

    input {
        height: 46px; }

    textarea {
        height: 184px; }

    input,
    textarea {
        width: 100%;
        padding: 12px 10px;
        border: 1px solid $gray;
        border-radius: 5px;

        &:focus,
        &:active {
            border-color: $black; } } }

@include media-breakpoint-up(xl) {
    .shopping-cart__contacts {
        margin-left: 15px;

        &-form {
            width: 650px; }

        &-row {
            gap: 20px; } } }

@include media-breakpoint-down(xl) {
    .shopping-cart__contacts {
        &-form {
            width: 100%; }

        &-row {
            gap: 30px; } } }

@include media-breakpoint-down(md) {
    .shopping-cart__contacts {
        h2 {
            font-size: 28px; }

        &-row {
            flex-direction: column;
            gap: 20px; } } }
