.shopping-cart__payment {
    h2 {
        font-size: 32px;
        line-height: 130%;
        font-weight: bold;
        margin-bottom: 10px; }

    &-methods {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        row-gap: 20px;
        column-gap: 30px;
        margin-top: 10px; }

    &-method {
        label {
            height: 76px;
            display: flex;
            gap: 10px;
            padding: 20px;
            border: 1px solid $gray;
            border-radius: 10px;
            cursor: pointer;
            transition: all 0.3s ease;

            h3 {
                font-size: 16px;
                line-height: 18px; } }

        .circle {
            flex-shrink: 0;
            height: 20px;
            width: 20px;
            border: 1px solid $black;
            border-radius: 50%; }

        input {
            display: none;

            &:checked ~ label {
                border-color: $primary;

                & .circle {
                    border: 4px solid $primary; } } } } }

@include media-breakpoint-up(xl) {
    .shopping-cart__payment {
        &-method {
            label {
                width: 255px; } } } }

@include media-breakpoint-only(lg) {
    .shopping-cart__payment {
        &-method {
            label {
                width: 290px; } } } }

@include media-breakpoint-only(md) {
    .shopping-cart__payment {
        &-method {
            label {
                width: 330px; } } } }

@include media-breakpoint-down(md) {
    .shopping-cart__payment {
        h2 {
            margin-bottom: 20px;
            font-size: 28px; }

        &-method {
            width: 100%;

            label {
                width: 100%; } } } }
