@import "./result-list/result-list";

.search {
    &__main {
        display: flex;
        gap: 30px; }

    .search__form {
        position: relative;
        width: fit-content;
        height: fit-content;
        margin-bottom: 20px;

        input {
            width: 539px;
            height: 50px;
            padding: 16px 50px 16px 20px;
            border: 1px solid #B4B4B4;
            border-radius: 5px;
            font-size: 18px;
            line-height: 100%; } }

    &__btn-container {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 0px;
        right: 0px;
        width: 50px;
        height: 50px;
        cursor: default;

        .btn {
            width: 29px;
            height: 29px;
            background: transparent;
            color: $primary;

            .icon {
                width: 100%;
                height: 100%; } } } }

@include media-breakpoint-down(xl) {
    .search {
        &__main {
            flex-direction: column; } } }

@include media-breakpoint-down(md) {
    .search {
           .search__form {
                width: 100%;

                input {
                    width: 100%; } } } }
