#main-slider {

    .splide__slide {
        position: relative;
        overflow: hidden; }

    .splide__arrow {
        position: absolute;
        height: 41px;
        width: 41px;
        background: transparent;
        border-radius: 50%;
        opacity: 1;
        color: #fff;
        outline: none !important;
        z-index: 10;

        & svg {
            height: 15px;
            width: 10px;

            fill: currentColor; }

        &:hover:not(:disabled) {
            color: #000;
            background: #fff;
            opacity: 1; } }

    .splide__arrow--next svg {
        transform: scale(-1, 1); }

    .splide__pagination {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);

        &__page {
            border-radius: 5px;
            margin: 0 2.5px;
            height: 3px;
            width: 25px;
            opacity: .5;


            &.is-active {
                transform: none !important;
                opacity: .9; } } }


    .main-slider {

        &__background {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: 0; }


        &__content-container {
            position: absolute;
            display: flex;
            flex-direction: column;
            color: #fff;
            z-index: 1; }


        &__content-type {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 19px;
            width: 55px;
            background: #e40000;
            border-radius: 4px;
            font-size: 14px; }

        &__content-button {
            height: 39px;
            width: 120px;
            background: transparent;
            border: 2px solid #fff;
            border-radius: 8px;
            font-weight: 300;
            font-size: 16px;
            color: #fff; } } }


@include media-breakpoint-up(xl) {
    #main-slider {
        max-width: 2048px !important;
        margin-top: 19px;


        .splide__slide {
            height: 424px; }


        .splide__arrow {
            top: 191.77px;
            border: 3px solid #fff;

            & svg {
                height: 23px;
                width: 13px; }

            &--prev svg {
                margin-right: 4px; }


            &--next svg {
                margin-left: 4px; } }

        .splide__pagination {
            bottom: 26px; }

        .main-slider {

            &__content-container {
                width: 500px;
                top: 104.5px;
                left: 189px;
                gap: 30px; }

            &__content-title {
                font-size: 40px;
                line-height: 120%; } } } }

