            &__arrow {
                top: 191.77px;
                border: 2px solid #fff;

                &--prev svg {
                    margin-right: 4px; }


                &--next svg {
                    margin-left: 4px; } } }

        .main-slider {

            &__content-container {
                width: 450px;
