//header main
.header {
    &__main {
        justify-content: space-between;
        align-items: center;
        height: 70px;

        .left {
            align-items: flex-end; }

        .header__burger-menu-btn {
            align-self: center;
            width: 35px;
            height: 35px;
            color: $white;
            border-radius: 5px; } }

    &__logo {
        display: inline-block;
        color: $black; }

    &__logo-description {
        display: inline-block;
        width: 75px;
        color: $gray;
        line-height: 110%; }

    &__addresses {
        gap: 30px; }

    &__address {
        h3 {
            font-size: 18px;
            font-weight: bold;
            color: $black;
            line-height: 100%;
            letter-spacing: -0.0046em; }

        span {
            font-size: 14px;
            color: $gray;
            line-height: 100%; } }

    &__call-request {
        flex-direction: column;
        gap: 6px;

        a {
            font-size: 18px;
            font-weight: bold;
            color: $primary;
            line-height: 100%; }

        span {
            font-size: 14px;
            color: $gray;
            line-height: 100%; } }

    &__icon-btn-group {
        gap: 20px; }

    &__icon-btn {
        position: relative;
        color: $black;

        .badge {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 2px 0 0 0;
            background: $primary;
            border-radius: 50%; } } }


@include media-breakpoint-up(xl) {
    .header {
        &__main {
            margin-top: 10px;

            .left {
                gap: 30px; }

            .right {
                gap: 54px; }

            .header__burger-menu-btn {
                display: none !important; } }

        &__logo-container {
            align-items: center;
            gap: 10px; }

        &__logo {
            .icon {
                width: 170px;
                height: 46.79px; } }

        &__logo-description {
            font-size: 14px; }

        &__icon-btn {
            .icon {
                width: 39px;
                height: 39px; }

            .icon-heart {
                width: 43px; }

            .badge {
                top: -5px;
                right: -5px;
                width: 25px;
                height: 25px; } } } }

@include media-breakpoint-down(xl) {
    .header {
        &__main {
            .left {
                gap: 15px; }

            .header__addresses {
                display: none !important; } }

        &__logo-container {
            align-items: flex-end;
            gap: 20px; }

        &__logo {
            .icon {
                width: 150px;
                height: 41.28px; } }

        &__logo-description {
            font-size: 11px; }



        &__icon-btn {
            .icon {
                width: 35px;
                height: 35px; }

            .icon-heart {
                width: 38.59px; }

            span {
                top: -3px;
                right: -5px;
                width: 20px;
                height: 20px;
                font-size: 10px; } } } }

@include media-breakpoint-only(lg) {
    .header {
        &__main {
            .right {
                gap: 86px; } } } }

@include media-breakpoint-down(lg) {
    .header {
        &__main {
            .right {
                gap: 46px; } } } }


@include media-breakpoint-down(md) {
    .header {
        &__main {
            .header__call-request {
                display: none !important; } }

        &__logo-description {
            display: none; }

        &__icon-btn {
            .icon {
                width: 30px;
                height: 30px; }

            .icon-heart {
                width: 33px; }

            .badge {
                top: -7px; } } } }
