.choosing-reason {
    .section__content {
        display: flex;
        gap: 30px;

        .left {
            width: 100%;
            font-size: 16px;
            line-height: 118%;
            color: $black;

            p:not(:last-child) {
                margin-bottom: 20px; }

            ol {
                margin: 0 0 20px 24px; }

            li {
                list-style: disc;

                &::marker {
                    font-size: 10px; } } }

        .right {
            display: flex;
            flex-direction: column;
            gap: 20px;
            flex-shrink: 0;
            width: 350px;
            height: fit-content;
            padding: 20px;
            background: #F4F4F4;
            border-radius: 15px;

            .item {
                display: flex;
                gap: 10px;
                width: 100%;

                &__icon {
                    flex-shrink: 0;
                    width: 35px;
                    height: 35px;
                    border-radius: 50%;
                    background: #FCFCFC;
                    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);

                    .icon {
                        width: 20px;
                        height: 14px;
                        fill: none !important; } }

                &__description {
                    width: 100%;
                    color: $black;

                    h3 {
                        margin-bottom: 8px;
                        font-size: 19px;
                        font-weight: bold;
                        line-height: 100%; }

                    span {
                        display: inline-block;
                        font-size: 16px;
                        line-height: 117%; } } } } } }

@include media-breakpoint-up(xl) {
    .choosing-reason {
        .section__content {
            .left {
                line-height: 117%; } } } }

@include media-breakpoint-down(xl) {
    .choosing-reason {
        .section__content {
            flex-direction: column;

            .right {
                flex-direction: row;
                flex-wrap: wrap;
                gap: 30px;
                width: 100%;
                padding: 30px;

                .item {
                    width: 420px; } } } } }

@include media-breakpoint-only(md) {
    .choosing-reason {
        .section__content {
            .right {
                .item {
                    width: 300px; } } } } }

@include media-breakpoint-down(md) {
    .choosing-reason {
        .section__content {
            .right {
                gap: 20px; } } } }
