#product-reviews {
    .splide {
        &__slide {
            img {
                width: 100%;
                border-radius: 8px; } }

        &__arrow {
            top: 223px; } } }

@include media-breakpoint-down(xl) {
    #product-reviews {
        .splide {
            &__slide {
                width: 290px; } } } }

@include media-breakpoint-only(lg) {
    #product-reviews {
        .splide {
            &__arrow {
                top: -3px; } } } }

@include media-breakpoint-down(md) {
    #product-reviews {
        .splide {
            &__track {
                overflow: visible; }

            &__slide {
                width: 300px; } } } }
