.shopping-cart__delivery {
    h2 {
        font-size: 32px;
        line-height: 130%;
        font-weight: bold;
        margin-bottom: 11px; }

    &-methods {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        row-gap: 20px;
        column-gap: 30px;
        margin-top: 10px; }

    &-method {
        label {
            height: 115px;
            display: flex;
            flex-direction: column;
            padding: 20px;
            border: 1px solid $gray;
            border-radius: 10px;
            cursor: pointer;
            transition: all 0.3s ease; }



        .circle {
            flex-shrink: 0;
            height: 20px;
            width: 20px;
            border: 1px solid $black;
            border-radius: 50%; }

        input {
            display: none;

            &:checked ~ label {
                border-color: $primary;

                & .circle {
                    border: 4px solid $primary; } } } }

    &-row {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            flex-wrap: nowrap;
            gap: 10px; }

    &-text {
        h3 {
            font-size: 16px;
            line-height: 18px; }

        span {
           display: inline-block;
           height: 36px;
           font-size: 14px;
           line-height: 130%;
           color: $gray; } }

    img {
        width: 60px;
        height: 40px; }

    &-bottom-text {
        margin-left: 30px;
        font-size: 16px;
        line-height: 18px;
        white-space: nowrap;
        color: #00A34E; }

    &-addresses {
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 20px;
        margin-block: 10px 20px;
        background: #F4F4F4;
        border-radius: 10px; }

    &-address {
        margin-bottom: 20px;

        label {
            display: flex;
            gap: 10px;
            cursor: pointer; }

        span {
            color: $black;
            font-size: 16px;
            line-height: 20px; }

        .circle {
            flex-shrink: 0;
            height: 20px;
            width: 20px;
            border: 1px solid $black;
            border-radius: 50%; }

        input {
            display: none;

            &:checked ~ label {
                & .circle {
                    border: 4px solid $black; } } } }

    &-map {
        width: 425px;
        height: 200px;
        border-radius: 5px;
        background: $gray;
        overflow: hidden; } }


@include media-breakpoint-up(xl) {
    .shopping-cart__delivery {
        &-method {
            label {
                width: 350px; } }

        &-text {
            width: 220px; }

        &-map {
            width: 425px; } } }

@include media-breakpoint-only(lg) {
    .shopping-cart__delivery {
        &-method {
            label {
                width: 290px; } }

        &-text {
            width: 140px; }

        img {
            margin-left: 10px; }

        &-map {
            width: 350px; } } }

@include media-breakpoint-down(lg) {
    .shopping-cart__delivery {
        & > .shopping-cart__delivery-method {
            label {
                width: 100%; } } } }

@include media-breakpoint-only(md) {
    .shopping-cart__delivery {
        &-method {
            label {
                width: 330px; } }

        &-text {
            width: 180px; }

        img {
            margin-left: 10px; }

        &-map {
            width: 430px; } } }

@include media-breakpoint-down(md) {
    .shopping-cart__delivery {
        h2 {
            font-size: 28px; }

        &-method {
            width: 100%;

            label {
                width: 100%; } }

        &-text {
            width: 220px; }

        &-addresses {
            flex-direction: column; }

        &-map {
            width: 100%; } } }
